@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/manrope-regular.ttf");
  font-display: swap;
}
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/manrope-medium.ttf");
  font-display: swap;
}
@font-face {
  font-family: "Manrope";
  font-style: bold;
  font-weight: 700;
  src: url("../fonts/manrope-bold.ttf");
  font-display: swap;
}
@font-face {
  font-family: "Manrope";
  font-style: bold;
  font-weight: 800;
  src: url("../fonts/manrope-extrabold.ttf");
  font-display: swap;
}
.button {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 0;
  text-decoration: none;
  text-align: center;
  font: inherit;
  color: inherit;
  background-color: inherit;
  cursor: pointer;
}
.wrapper {
  padding: 0 75px;
}
@media (min-width: 1300px) {
  .wrapper {
    padding: 0 30px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
.page {
  min-width: 430px;
  height: 100%;
  font-family: "Manrope", sans-serif;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 0;
  color: #606060;
  background-color: #ffffff;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0;
}
.logo {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 0;
  text-decoration: none;
  text-align: center;
  font: inherit;
  color: inherit;
  background-color: inherit;
  cursor: pointer;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: left;
}
.logo--footer {
  font-size: 24px;
  line-height: 30px;
  font-weight: 800;
}
.app-button {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 0;
  text-decoration: none;
  text-align: center;
  font: inherit;
  color: inherit;
  background-color: inherit;
  cursor: pointer;
  width: 240px;
  padding: 16px 20px 17px 68px;
  color: #ffffff;
  background-color: #73C1F2;
  border-radius: 25px;
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  text-align: left;
  background-position: left 28px center;
  background-repeat: no-repeat;
}
.app-button--small {
  width: 50px;
  height: 50px;
  padding: 0;
  border-radius: 50%;
  background-position: center;
}
.app-button--call {
  background-image: url("../../img/buttons/call.svg");
  background-size: 20px 20px;
}
.app-button--catalog {
  background-image: url("../../img/buttons/catalog.svg");
  background-size: 20px 20px;
}
.app-button--staff {
  background-image: url("../../img/buttons/staff.svg");
  background-size: 24px 24px;
}
.app-button:hover {
  background-color: #0487D9;
}
.app-button:active {
  background-color: #0487D9;
  opacity: 0.3;
}
.contacts__title {
  align-items: center;
  margin: 0;
  font-size: 32px;
  line-height: 39px;
  font-weight: 800;
  text-transform: uppercase;
  text-align: center;
}
.contacts__list {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  gap: 23px;
  width: 240px;
  font-size: 16px;
  line-height: 22px;
  font-weight: 500;
}
@media (min-width: 1300px) {
  .contacts--desktop-nav .contacts__list {
    font-size: 14px;
    line-height: 19px;
    font-weight: 500;
  }
}
.contacts__item {
  position: relative;
  padding-left: 29px;
}
.contacts__item--email {
  font-size: 14px;
  line-height: 19px;
  font-weight: 500;
}
@media (min-width: 1300px) {
  .contacts--desktop-nav .contacts__item--adress {
    max-width: 193px;
    padding-left: 26px;
  }
  .contacts--desktop-nav .contacts__item--schedule {
    padding-left: 31px;
  }
  .contacts--desktop-nav .contacts__item--phone {
    padding-left: 28px;
  }
  .contacts--desktop-nav .contacts__item--email {
    padding-left: 26px;
  }
}
.contacts__icon {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto 0;
}
.contacts__schedule-list {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style-type: none;
}
.contacts__phone-list {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style-type: none;
}
.contacts__phone-link {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 0;
  text-decoration: none;
  text-align: center;
  font: inherit;
  color: inherit;
  background-color: inherit;
  cursor: pointer;
}
.contacts__link {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 0;
  text-decoration: none;
  text-align: center;
  font: inherit;
  color: inherit;
  background-color: inherit;
  cursor: pointer;
  text-align: left;
}
.contacts__link:hover {
  color: #0487D9;
}
.contacts__link:active {
  color: #0487D9;
  opacity: 0.3;
}
.social__list {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style-type: none;
}
.social__link {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 0;
  text-decoration: none;
  text-align: center;
  font: inherit;
  color: inherit;
  background-color: inherit;
  cursor: pointer;
  width: 42px;
  height: 42px;
  border-radius: 2px;
  background-color: #ffffff;
  background-repeat: no-repeat;
  background-position: center;
}
.social__link--vk {
  background-image: url("../../img/social/vk.svg");
  background-size: 21px 13px;
}
.social__link--vk:hover {
  background-color: #1A76FF;
}
.social__link--vk:active {
  background-color: #1A76FF;
  opacity: 0.3;
}
.social__link--whatsapp {
  background-image: url("../../img/social/whatsapp.svg");
  background-size: 26px 26px;
}
.social__link--whatsapp:hover {
  background-color: #00CD4C;
}
.social__link--whatsapp:active {
  background-color: #00CD4C;
  opacity: 0.3;
}
.social__link--viber {
  background-image: url("../../img/social/viber.svg");
  background-size: 24px 23px;
}
.social__link--viber:hover {
  background-color: #7360F2;
}
.social__link--viber:active {
  background-color: #7360F2;
  opacity: 0.3;
}
@media (min-width: 1300px) {
  .social--desktop-nav .social__link {
    width: 30px;
    height: 30px;
  }
  .social--desktop-nav .social__link--vk {
    background-size: 15px 9px;
  }
  .social--desktop-nav .social__link--whatsapp {
    background-size: 18px 18px;
  }
  .social--desktop-nav .social__link--viber {
    background-size: 15px 15px;
  }
}
.social--footer .social__link {
  border-radius: 5px;
  border: 1px solid #606060;
}
.site-menu__list {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style-type: none;
  font-size: 14px;
  line-height: 24px;
  font-weight: 500;
}
.site-menu__link {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 0;
  text-decoration: none;
  text-align: center;
  font: inherit;
  color: inherit;
  background-color: inherit;
  cursor: pointer;
  padding: 5px 0;
  text-align: left;
}
.site-menu__link:hover {
  color: #0487D9;
}
.site-menu__link:active {
  color: #0487D9;
  opacity: 0.3;
}
@media (min-width: 1300px) {
  .site-menu__link {
    padding: 16px 0 17px 0;
  }
}
.page-header {
  box-sizing: border-box;
  min-height: 804px;
  color: #ffffff;
  background-color: #606060;
  background-image: url("../../img/back.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.page-header__content {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 18px;
}
@media (min-width: 1300px) {
  .page-header__content {
    padding-top: 81px;
  }
}
.page-header__tittle {
  box-sizing: border-box;
  width: 327px;
  padding-left: 7px;
  margin: 0;
  margin-bottom: 30px;
  font-size: 40px;
  line-height: 50px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 1300px) {
  .page-header__tittle {
    width: 593px;
    margin-bottom: 24px;
    padding-left: 0;
    font-size: 65px;
    line-height: 75px;
  }
}
.page-header__phone {
  order: -1;
  margin-bottom: 34px;
}
@media (min-width: 1300px) {
  .page-header__phone {
    display: none;
  }
}
.page-header__phone-list {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style-type: none;
  padding-top: 40px;
  background-image: url("../../img/phone.svg");
  background-size: 30px 30px;
  background-position: top center;
  background-repeat: no-repeat;
  font-size: 30px;
  line-height: 41px;
  font-weight: 800;
}
.page-header__phone-link {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 0;
  text-decoration: none;
  text-align: center;
  font: inherit;
  color: inherit;
  background-color: inherit;
  cursor: pointer;
}
.page-header__buttons-container {
  display: flex;
  flex-direction: column;
  gap: 18px;
}
@media (min-width: 1300px) {
  .page-header__buttons-container {
    flex-direction: row;
    gap: 22px;
  }
}
@media (max-width: 1299px) {
  .page-header__button--staff {
    display: none;
  }
}
@media (min-width: 1300px) {
  .page-header__button--call {
    position: absolute;
    top: 0;
    right: 30px;
    transform: translateY(-100%);
  }
}
.main-nav {
  position: relative;
}
@media (max-width: 1299px) {
  .main-nav--opened .main-nav__bar {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    background-color: #73C1F2;
  }
}
.main-nav__top-bar {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 75px;
  padding-top: 26px;
  padding-bottom: 26px;
}
@media (min-width: 1300px) {
  .main-nav__top-bar {
    padding: 0 30px;
  }
}
.main-nav__top-bar::after {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 0;
  height: 0;
  content: "";
}
@media (min-width: 1300px) {
  .main-nav__top-bar {
    padding-top: 22px;
    padding-bottom: 21px;
  }
  .main-nav__top-bar::after {
    width: 100%;
    border-bottom: 1px solid #ffffff;
  }
}
@media (max-width: 1299px) {
  .main-nav--opened .main-nav__top-bar {
    flex-direction: column;
    padding-top: 82px;
    padding-bottom: 33px;
  }
  .main-nav--opened .main-nav__top-bar::after {
    width: 250px;
    border-bottom: 2px solid #ffffff;
    margin: 0 auto;
  }
}
@media (min-width: 1300px) {
  .main-nav__toggle-container {
    display: none;
  }
}
.main-nav--opened .main-nav__toggle-container {
  position: absolute;
  top: 13px;
  right: 35px;
  z-index: 1;
}
.main-nav__toggle {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 0;
  text-decoration: none;
  text-align: center;
  font: inherit;
  color: inherit;
  background-color: inherit;
  cursor: pointer;
  width: 42px;
  height: 37px;
}
.main-nav__toggle:hover {
  color: #73C1F2;
}
.main-nav__toggle:active {
  color: #73C1F2;
  opacity: 0.3;
}
.main-nav--opened .main-nav__toggle {
  position: relative;
  width: 40px;
  height: 40px;
  color: #73C1F2;
  border-radius: 50%;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle:hover {
  color: #0487D9;
}
.main-nav--opened .main-nav__toggle:active {
  color: #73C1F2;
  opacity: 0.3;
}
.main-nav__toggle-icon--close {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.main-nav--closed .main-nav__toggle-icon--close {
  display: none;
}
.main-nav--opened .main-nav__toggle-icon--open {
  display: none;
}
.main-nav__logo-container {
  display: none;
}
@media (min-width: 1300px) {
  .main-nav__logo-container {
    display: block;
    width: 146px;
    margin-right: 105px;
  }
}
.main-nav__contacts {
  display: none;
}
@media (min-width: 1300px) {
  .main-nav__contacts {
    display: flex;
    align-items: center;
  }
}
@media (max-width: 1299px) {
  .main-nav--opened .main-nav__contacts {
    display: block;
    width: 280px;
  }
}
.main-nav__contacts-title {
  margin-bottom: 22px;
}
@media (min-width: 1300px) {
  .main-nav__contacts-title {
    display: none;
  }
}
@media (min-width: 1300px) {
  .main-nav__contacts-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
