  }
}
.main-nav__bottom-bar {
  display: none;
  padding: 0 75px;
}
@media (min-width: 1300px) {
  .main-nav__bottom-bar {
    padding: 0 30px;
  }
}
@media (min-width: 1300px) {
  .main-nav__bottom-bar {
    display: flex;
    flex-direction: column;
    align-items: start;
    padding-top: 20px;
  }
}
@media (max-width: 1299px) {
  .main-nav--opened .main-nav__bottom-bar {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 25px;
  }
}
.main-nav__site-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 280px;
}
@media (min-width: 1300px) {
  .main-nav__site-list {
    flex-direction: row;
    justify-content: space-between;
    width: 904px;
  }
}
@media (max-width: 1299px) {
  .main-nav--opened .main-nav__social {
